import "./Userstyle.css";
import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Logo from "../assets/foodiKo1.png";
import HomeIcon from "@mui/icons-material/Home";
import PersonIcon from "@mui/icons-material/Person";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";

const Login = () => {
  const [typelgn, setTypelgn] = useState("password");
  const [iconlgn, setIconlgn] = useState(<VisibilityOffIcon />);

  const handleTogglelgn = () => {
    if (typelgn === "password") {
      setIconlgn(<VisibilityIcon />);
      setTypelgn("text");
    } else {
      setIconlgn(<VisibilityOffIcon />);
      setTypelgn("password");
    }
  };

  const navigate = useNavigate();
  const handleSubmit = (e) => {
    e.preventDefault();
    navigate("/");
  };

  return (
    <div className="user-container">
      <div className="n-nav">
        <div className="n-foodikologo">
          <img src={Logo} alt="foodiko" />
        </div>
        <Link className="n-link" to="/">
          <button>
            <span>
              <HomeIcon />
            </span>
            <span>Home</span>
          </button>
        </Link>
      </div>
      <div className="user-cont">
        <form className="user-form" onSubmit={(e) => handleSubmit(e)}>
          <p className="u-name">Login</p>
          <div className="u-input">
            <span>
              <label htmlFor="username">Username</label>
            </span>
            <span className="u-icon">
              <PersonIcon />
            </span>
            <br></br>
            <input type="text" id="username" required></input>
            <div className="screen1" id="screen1"></div>
          </div>
          <div className="u-input">
            <span>
              <label htmlFor="password">Password</label>
            </span>
            <span className="u-icon u-eyeicon" onClick={handleTogglelgn}>
              {iconlgn}
            </span>
            <br></br>
            <input type={typelgn} id="password" required></input>
            <div className="screen2"></div>
            <div className="u-link2">
              <Link to="/forgotpassword" className="u-font1">
                Forgot Password?
              </Link>
            </div>
          </div>
          <div className="u-btn1">
            <button type="submit" className="u-btn">
              login
            </button>
          </div>
          <div className="u-link1">
            <span className="u-font2">Don't have an account? </span>
            <span>
              <Link to="/signup" className="u-font1">
                Sign Up
              </Link>
            </span>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
