import "./Userstyle.css";
import { Link, useNavigate } from "react-router-dom";
import Logo from "../assets/foodiKo1.png";
import HomeIcon from "@mui/icons-material/Home";
import EmailIcon from "@mui/icons-material/Email";
import DialpadIcon from "@mui/icons-material/Dialpad";

const Forgotpwd = () => {
  const navigate = useNavigate();
  const handleSubmit = (e) => {
    e.preventDefault();
    navigate("/resetpassword");
  };

  return (
    <div className="user-container">
      <div className="n-nav">
        <div className="n-foodikologo">
          <img src={Logo} alt="foodiko" />
        </div>
        <Link className="n-link" to="/">
          <button>
            <span>
              <HomeIcon />
            </span>
            <span>Home</span>
          </button>
        </Link>
      </div>
      <div className="user-cont">
        <form className="user-form" onSubmit={(e) => handleSubmit(e)}>
          <p className="u-name">Forgot Password</p>
          <div className="u-input">
            <span>
              <label htmlFor="email">Email</label>
            </span>
            <span className="u-icon">
              <EmailIcon />
            </span>
            <br></br>
            <input type="email" id="email" required></input>
            <div className="screen1"></div>
          </div>
          <div style={{ width: "85%", height: "38px",display:"flex",alignItems:"center",justifyContent:"flex-end" }}>
            <button type="button" className="u-send">
              Send
            </button>
          </div>
          <div className="u-input">
            <span>
              <label htmlFor="otp">OTP</label>
            </span>
            <span className="u-icon">
              <DialpadIcon />
            </span>
            <br></br>
            <input type="text" id="otp" required></input>
            <div className="screen2"></div>
          </div>
          <div className="u-btn1">
            <button type="submit" className="u-btn">
              Confirm
            </button>
          </div>
          <div className="u-link1">
            <span className="u-font2">Don't receive an OTP? </span>
            <span className="u-font1">Resend</span>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Forgotpwd;
